.counter-container {
    text-align: center;
  }
  
  .counter-container h1 {
    font-size: 2.5em;
    color: #333; /* Heading color */
  }
  
  .counter-container p {
    font-size: 1.2em;
    color: #666; /* Text color */
  }
  
  .counter-container h2 {
    font-size: 2em;
    color: #333; /* Counter value color */
  }
  
  .counter-container button {
    font-size: 1.2em;
    padding: 10px 20px;
    margin: 0 10px;
    background-color:  hsl(143, 100%, 35%); /* Button background color */
    color: #fff; /* Button text color */
    border: none;
    border-radius: 5px;
    cursor: pointer;
  }
  
  .counter-container button:hover {
    background-color: hsl(143, 100%, 35%); /* Button background color on hover */
  }
  
  .error {
    color: red !important; /* Error message color */
  }